<template>
    <div class="results-block">
        <p class="">Completed the game in {{ timeResult }} ms</p>
        <p :class="captionClass">{{ caption }}</p>
    </div>
</template>

<script>
export default {
    props: [
        "timeResult",
    ],
    data() {
        return {
            caption: '',
            captionClass: '',
        }
    },
    mounted() {
        if (this.timeResult < 450) {
            this.caption = 'Rapid responder!';
            this.captionClass = 'success';
        } else if (this.timeResult < 1000) {
            this.caption = 'Medium speed responder!';
            this.captionClass = 'warning';
        } else {
            this.caption = 'Slow responder!';
            this.captionClass = 'error';
        }
    }
}
</script>

<style>
.success {
    color: rgb(55, 248, 168)
}

.warning {
    color: rgb(232, 200, 18)
}

.error {
    color: rgb(232, 54, 18)
}

.success,
.warning,
.error {
    font-weight: bold;
    font-size: 20px;
    padding: 5%;
}
</style>