<template>
  <div class="game-block" v-if="isVisible" @click="finishGame">
    <p>Click me here as fast as you can :) </p>
  </div>
</template>

<script>
export default {
    props: [
        "delay",
    ],
    data () {
        return {
            isVisible: false,
            startTime: null,
            timer: 0,
        }
    },
    methods: {
        finishGame () {
            this.timer = new Date().valueOf() - this.startTime
            console.log(this.timer);
            this.$emit('timer',  this.timer)
        },
    },
    mounted () {
        console.log(this.delay)
        setTimeout(() => {
            this.isVisible = true
            this.startTime = new Date().valueOf()
        }, this.delay);
    },
}
</script>

