<script src="./Learn.js" />
<style src="./Learn.scss" lang="scss"></style>

<template>
    <div class="learn-div">
        <BooksBanner />
        <div class="game">
            <button class="game-button" @click="playGame" :disabled="isPlaying">Play!</button>
            <Block v-if="isPlaying" :delay="delay" @timer="showResults"/>
            <Result v-if="isResults" :timeResult="timeResult"/>
        </div>
        <div class="books-section">
            <button @click="toggleShowBooks" class="hide-button">
            <span>{{buttonTitle}}</span> books
        </button>
        <div v-if="showBooks">
            <ul class="books-block">
                <li v-for="book in books" :class="{ fav : book.isFav }" @click="toogleFav(book)" :key="book.title">
                    {{book.title}} by {{book.author}}. <a :href="book.buyLink" target="new">Buy here</a>
                </li>
            </ul>
        </div>
        </div>
        <br/>
        <div class="block" @mouseover="handleMouseEvent">Mouse enter</div>
        <div class="block" @mouseleave="handleMouseEvent">Mouse leave</div>
        <div class="block" @dblclick="handleMouseEvent">Double click</div>
        <div class="block" @mousemove="handleMouseMove">Mouse position - {{x}} and {{y}}</div>
    </div>

</template>